.depositHistory {
  margin: 0 auto;
  text-align: left;
  width: 960px; }
  .depositHistory input, .depositHistory select {
    border: 1px solid #DEE1E5; }
  .depositHistory .orderBy {
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
    width: 15px;
    height: 15px;
    padding: 0;
    margin: 0;
    position: relative;
    top: 5px;
    left: 5px; }
  .depositHistory .ordered {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg); }
  .depositHistory .labelHolder {
    position: relative;
    top: 8px; }
  .depositHistory .pageTitle {
    font-size: 26px;
    text-align: left;
    margin: 0;
    color: #4f5858;
    padding: 35px 0; }
  .depositHistory .dateSearchHolder {
    height: 96px; }
    .depositHistory .dateSearchHolder .start, .depositHistory .dateSearchHolder .end {
      display: inline-block;
      padding: 22px 32px; }
    .depositHistory .dateSearchHolder .start {
      background: #F5F5F5;
      float: left; }
    .depositHistory .dateSearchHolder .end {
      background: #EAEAEA;
      float: right; }
    .depositHistory .dateSearchHolder .dateicon {
      width: 30px !important; }
    .depositHistory .dateSearchHolder .iconHolder {
      background: #FFF;
      display: inline-block;
      padding: 2px 6px;
      margin: 0; }
      .depositHistory .dateSearchHolder .iconHolder .calendarIcon {
        display: inline-block;
        width: 27px; }
  .depositHistory .doiSearchHolder {
    height: 32px;
    padding: 30px 0 18px;
    width: 100%; }
    .depositHistory .doiSearchHolder .doiSearch {
      border: 1px solid #DEE1E5;
      float: right;
      height: 32px;
      width: 460px; }
  .depositHistory .historyData {
    border-spacing: 0;
    margin: 0;
    padding: 20px 0 0;
    width: 100%; }
    .depositHistory .historyData tr th {
      border: 1px solid #DEE1E5;
      border-left: 0px;
      border-right: 0px;
      padding: 8px 0; }
    .depositHistory .historyData tr td {
      padding-top: 32px; }
    .depositHistory .historyData tr .first {
      border-left: 1px solid #DEE1E5;
      padding: 8px 0 8px 32px; }
    .depositHistory .historyData tr .last {
      border-right: 1px solid #DEE1E5; }
    .depositHistory .historyData tr .firstTd {
      padding: 32px 0 0 32px; }
  .depositHistory .pagination {
    display: inline-table;
    float: right;
    margin-top: 40px; }
    .depositHistory .pagination li {
      cursor: pointer;
      display: block;
      height: 37px;
      text-align: center;
      float: left;
      margin: 0 2px 0 0; }
      .depositHistory .pagination li a {
        border: 1px solid #CBCBCB;
        display: table-cell;
        margin: 0 2px 0 0;
        height: 37px;
        min-width: 40px;
        padding: 5px;
        vertical-align: middle; }
    .depositHistory .pagination .active a {
      border: 1px solid #000; }
    .depositHistory .pagination .next a {
      margin: 0; }
      .depositHistory .pagination .next a .nex {
        -webkit-transform: rotate(90deg);
        -moz-transform: rotate(90deg);
        -o-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
        transform: rotate(90deg);
        width: 20px;
        height: 20px;
        padding: 0;
        margin: 0;
        position: relative;
        top: 3px;
        right: 1px; }
    .depositHistory .pagination .previous a .prev {
      -webkit-transform: rotate(270deg);
      -moz-transform: rotate(270deg);
      -o-transform: rotate(270deg);
      -ms-transform: rotate(270deg);
      transform: rotate(270deg);
      width: 20px;
      height: 20px;
      padding: 0;
      margin: 0;
      position: relative;
      top: 3px;
      right: 1px; }

