body {
  margin: 0;
  padding: 0;
  -webkit-box-orient: vertical;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex; }

body, #root, .app {
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
