* {
  box-sizing: border-box; }

.loginButton {
  font-size: 16px; }

.userProfileMenuHolder {
  position: relative; }
  .userProfileMenuHolder .profileMenuTrigger {
    cursor: pointer; }
    .userProfileMenuHolder .profileMenuTrigger .profileActions {
      width: 15px;
      height: 15px;
      padding: 0;
      margin: 0;
      position: relative;
      top: 3px;
      -webkit-transform: rotate(180deg);
      -moz-transform: rotate(180deg);
      -o-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
      transform: rotate(180deg); }
    .userProfileMenuHolder .profileMenuTrigger .menuOpen {
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      transform: rotate(0deg); }
  .userProfileMenuHolder .profileMenu {
    position: absolute;
    width: 140px;
    color: #FFFFFF;
    background: #616B6B;
    line-height: 30px;
    text-align: center;
    top: 90px;
    left: -30px;
    padding: 20px 5px 5px; }
    .userProfileMenuHolder .profileMenu span {
      cursor: pointer; }
  .userProfileMenuHolder .profileMenu:after {
    content: '';
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -8px;
    width: 0;
    height: 0;
    border-bottom: 8px solid #616B6B;
    border-right: 8px solid transparent;
    border-left: 8px solid transparent; }

